<template>
  <div>
  <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>学生管理</el-breadcrumb-item>
      <el-breadcrumb-item>学生列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!-- 搜索 -->

        <el-row :gutter="10" class="el-row">
          <el-col :span="1.5">
            <el-button
              type="primary"
              icon="el-icon-plus"
              v-if="is_authority.is_user_add"
              size="mini"
              @click=";(open = true)"
              >新增</el-button
            >
              
          </el-col>
          
          <el-col :span="1.5">
            <el-button
              type="success"
              icon="el-icon-edit"
              size="mini"
              :disabled="single"
              @click="editUser"
            v-if="is_authority.is_user_upt"
              >修改</el-button
            >
            
          </el-col>
          <el-col :span="1.5">
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              :disabled="multiple"
            v-if="is_authority.is_user_del"
              @click="del"
              >删除</el-button
            >
            
          </el-col>
          <el-col :span="1.5">
            <el-button type="info" icon="el-icon-upload2" size="mini"
              >导入</el-button
            >
            <!-- @click="handleImport" -->
                      <!-- v-hasPermi="['system:user:import']" -->
          </el-col>
          <el-col :span="1.5">
            <el-button type="warning" icon="el-icon-download" size="mini"
              >导出</el-button
            >
            <!-- @click="handleExport"
                      v-hasPermi="['system:user:export']" -->
          </el-col>
          <!-- <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar> -->
        </el-row>

      
      <!-- 数据 -->
      <el-table
        :data="List"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50" align="center" />
		
        <el-table-column align="center" type="index" label="用户编号" width="80"></el-table-column>
        <el-table-column align="center" prop="cpName" label="职务名称"></el-table-column>
        <el-table-column align="center" prop="cpDemo" label="职务职能简介"></el-table-column>
		
        <el-table-column label="操作" width="120px" align="center">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editUser(scope.row)"
              ></el-button>
            </el-tooltip>
            <!-- 删除 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"  v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>

            
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
      <!-- 对话框 :before-close="handleClose" 在对话框关闭之前执行-->
      <!-- 标题 -->
      <el-dialog
        title="编辑"
        :visible.sync="open"
        width="50%"
        @close="closeInit"
      >
        <el-form ref="formRef" :rules="rules" :model="Form" label-width="100px">
          <el-form-item label="职务职能" prop="cpName">
            <el-input v-model="Form.cpName"></el-input>
          </el-form-item>

          <el-form-item label="职务职能简介" prop="cpDemo">
            <el-input v-model="Form.cpDemo"></el-input>
          </el-form-item>
        </el-form>

        <span slot ="footer" class="dialog-footer">
          <el-button @click="open=false" >取 消</el-button>
          <el-button @click="submitForm" type="primary">确 定</el-button>
        </span>
        
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default{
	data(){
		return{
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      open : false,
      Form:{},
			// 非单个禁用
			single: true,
			// 非多个禁用
			multiple: true,
			List:[],
			 queryVo:{
         cpName:undefined,
				 page: {
				   current: 1,
				   size: 5
				 }
			 },
			 page: {
			         current: 1,
			         size: 5,
			         total: 0
					},
       ids:[],
       rules:{
         cpName:[
           { required: true, message: '职务职能不能为空', trigger: 'blur' }
         ],
       }
		}
	},
	created() {
    this.isAuthority()
		this.queryPage()
  },
  computed: {
    ...mapState(['btus']),
  },
	methods:{
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    async editUser(row){
      this.open = true
      const id = row.id || this.ids
      const { data : res } = await this.$http.get(
        '/classPositonInfo/getByIdClassPositonInfo/' + id 
      )
      if(res.code != 20000)return this.$message.error(res.message)
      // console.log(res)
      this.Form = res.data.entity
    },
    submitForm(){
      this.$refs.formRef.validate(async valid =>{
        if(valid){
          const {data : res } = await this.$http.post(
            '/classPositonInfo/saveOrUpdateClassPositonInfo',
            this.Form
          )
          if (res.code!=20000) return this.$message.error(res.message)
          this.open = false 
          this.queryPage()
          return this.$message.success(res.message)

        }
      })
    },
    closeInit() {
      this.Form = {}
      this.$refs.formRef.resetFields()
    },

    del(row) {
      if(row.id){var a =row.id.split(',')}
      const userid = a || this.ids;
      console.log(userid);
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await this.$http.delete(
            '/classPositonInfo/removeByIdClassPositonInfos',
            {
              data: userid
            }
          )
          // console.log(res);
          this.queryPage()
          this.List = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },
		async queryPage() {
		      const { data: res } = await this.$http.post(
		        '/classPositonInfo/queryClassPositonInfoList',
		        this.queryVo
		      )
			  console.log(res.data.dataList.records)
		      if (res.code != 20000) return this.$message.error(res.message)
		      this.List = res.data.dataList.records
		      this.page.total = res.data.dataList.total	
		      this.page.current = res.data.dataList.current
		      this.page.size = res.data.dataList.size
		      // console.log(this.List)
		    },
		//选择框
		handleSelectionChange(selection) {
		  
		  this.ids = selection.map(item => item.id)
		  this.single = selection.length != 1
		  this.multiple = !selection.length
		},
		//更新状态
		async uptStatus(status) {
		  const { data: res } = await this.$http.put(
		    `/jxyxUser/users/${status.id}/state/${status.mgState}`
		  )
		  if (res.code != 20000) {
		    status.mgState = !status.mgState
		    return this.$message.error(res.message)
		  }
		  return this.$message.success(res.message)
		},
		// 监听每页展示的条数
		handleSizeChange(newSize) {
		  this.queryVo.page.size = newSize
		  this.queryPage()
		},
		//监听页码
		handleCurrentChange(newCurrent) {
		  this.queryVo.page.current = newCurrent
		  this.queryPage()
		},
	},
	
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
</style>
